/*footer*/
.footer{
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  margin-top: auto;
}
/* navbar CSS */

.navbar{
  font-family: "Oswald", sans-serif;
  font-optical-sizing: auto;
  font-weight: 500;
  font-style: normal;
  box-shadow: 0px;
}

/* page not found CSS */
.pnf{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.pnf-title{
  font-family: "Oswald", sans-serif;
  font-size: 150px;
  font-weight: 700;
}
.pnf-heading{
  font-family: "Oswald", sans-serif;
  font-weight: bolder;
}
.pnf-btn{
  font-family: "Oswald", sans-serif;
  font-size: larger;
  font-weight: 700;
  color: black;
  border: 3px solid black;
  text-decoration: none;
  padding: 10px;
  margin-top: 10px;
  box-shadow: none;
}
.pnf-btn:hover {
  background-color: black;
  color: white;
}
/*contact us*/
.contactus{
  font-family: "Oswald", sans-serif;
  font-size: larger;
}

/*about us*/
.aboutus{
  font-family: "Oswald", sans-serif;
  font-size: larger;
}

/*Register*/
.register{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}


/*login*/
.login{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.btn {
  background-color: #000000;
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
}


/*forgot password*/
.forgotpassword{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}